<template>
  <div class="app-foot">
    <div class="foot-inner">
      <div class="app-download">
        <div class="app-link-banner-title">APP</div>
        <div class="app-link-banner-description">60APPAPP</div>
        <div class="app-link-banner-btn">
          <a href=""><img src="../../static/images/ios.png" alt="APP"></a>
          <a href=""><img src="../../static/images/android.png" alt="APP"></a>
        </div>
        <img src="../../static/images/phone.png" alt="APP" class="banner-phone">
      </div>
    </div>
    <div class="foot-about">
      <p class="title">6060SEC</p>
      <p class="content">
        60
        
      </p>
      <p class="title">60</p>
      <p class="content">
        
        6060
      </p>
      <p class="title"></p>
      <p class="content">
        6060SEC
        
        
        60
      </p>
      <p class="title">60APP</p>
      <p class="content">
        
        APP
        
      </p>
    </div>
    <div class="foot-info">
      <div class="foot-info-content">
        <img src="../../static/images/logo.png" alt="logo" @click="gohome">
        <ul>
          <li></li>
          <li v-for="listitem in list">
            <router-link :to="{name:'App-classifyinfo',query:{type:listitem.type,name:listitem.name}}">
              {{listitem.name}}
            </router-link>
          </li>
        </ul>
        <ul>
          <li></li>
          <li><a @click="gohome"></a></li>
          <li>
            <router-link :to="{name: 'App-speciallist'}"></router-link>
          </li>
        </ul>
        <ul>
          <li></li>
          <li><a href=""></a></li>
          <li>
            <router-link :to="{name: 'App-protocol'}"></router-link>
          </li>
        </ul>
      </div>
    </div>
    <div class="foot-copyrights-inner">
      <div class="foot-copyrights-desc">6060SEC </div>
      <div class="foot-copyrights-copyrights">6060SECInc.</div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'app-foot',
    data() {
      return {
        list: [],
      }
    },
    created() {
      let self = this;
      /*--------------------  --------------------*/
      self.$http.get(self.app_config.basePath + '/foot', {
        params: {}
      }).then(function (response) {
        self.list = response.data.data.classify_info.list;
      }).catch(function (err) {
        console.log(err);
      });
    },
    methods: {
      gohome() {
        let self = this;
        if (self.$route.name === 'App-home') {
          document.documentElement.scrollTop = document.body.scrollTop = 0;
        } else {
          self.$router.push({name: 'App-home'});
        }
      }
    }
  }
</script>




<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

  .app-foot {
    width: 100%;
    min-width: 1000px;
    margin: 30px auto 70px;
  }

  .app-foot > .foot-inner {
    width: 100%;
    background-color: #f1f1f2;
    border-top: 1px solid #cfcfcf;
    border-bottom: 1px solid #cfcfcf;
  }

  .app-foot > .foot-inner > .app-download {
    position: relative;
    box-sizing: border-box;
    width: 1000px;
    margin: auto;
    padding: 40px 80px;
    height: 240px
  }

  .app-foot > .foot-inner > .app-download > .app-link-banner-title {
    font-size: 28px;
    color: #262626;
    letter-spacing: 1px;
    font-weight: 700;
  }

  .app-foot > .foot-inner > .app-download > .app-link-banner-description {
    width: 480px;
    margin-top: 20px;
    font-size: 16px;
    line-height: 1.5;
    letter-spacing: 2px;
    color: #5e5e5e;
  }

  .app-foot > .foot-inner > .app-download > .banner-phone {
    position: absolute;
    right: 80px;
    bottom: 0;
    display: block;
    width: 300px;
  }

  .app-foot > .foot-inner > .app-download > .app-link-banner-btn {
    position: absolute;
    left: 80px;
    bottom: 40px;
    height: 40px;
  }

  .app-foot > .foot-inner > .app-download > .app-link-banner-btn > a > img {
    width: 130px;
  }

  .app-foot > .foot-inner > .app-download > .app-link-banner-btn > a:last-child {
    margin-left: 30px;
  }

  .app-foot > .foot-about {
    width: 1000px;
    margin: auto;
    padding: 45px 0;
  }

  .app-foot > .foot-about > p {
    margin: auto;
  }

  .app-foot > .foot-about > p.title {
    font-size: 14px;
    font-weight: 700;
    margin-bottom: 10px;
  }

  .app-foot > .foot-about > p.content {
    font-size: 14px;
    line-height: 1.5;
    margin-bottom: 25px;
  }

  .app-foot > .foot-about > p:last-child {
    margin-bottom: 0;
  }

  .app-foot > .foot-info {
    border-top: 1px solid #cfcfcf;
    border-bottom: 1px solid #cfcfcf;
  }

  .app-foot > .foot-info > .foot-info-content {
    box-sizing: border-box;
    padding: 40px;
    height: 280px;
    width: 1000px;
    margin: auto;
    overflow: hidden;
  }

  .app-foot > .foot-info > .foot-info-content > img {
    margin-left: -40px;
    margin-top: 75px;
    height: 40px;
    display: block;
    float: left;
    cursor: pointer;
  }

  .app-foot > .foot-info > .foot-info-content > ul {
    list-style: none;
    display: block;
    float: left;
    margin-left: 40px;
  }

  .app-foot > .foot-info > .foot-info-content > ul > li {
    margin-bottom: 15px;
  }

  .app-foot > .foot-info > .foot-info-content > ul > li:first-child {
    font-weight: 700;
    margin-bottom: 25px;
  }

  .app-foot > .foot-info > .foot-info-content > ul > li > a {
    text-decoration: none;
    color: #262626;
    cursor: pointer;
  }

  .app-foot > .foot-copyrights-inner {
    width: 1000px;
    margin: auto;
    font-size: 12px;
    line-height: 70px;
  }

  .app-foot > .foot-copyrights-inner .foot-copyrights-desc {
    float: left;
  }

  .app-foot > .foot-copyrights-inner .foot-copyrights-copyrights {
    float: right;
  }
</style>



